* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #00bfff;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.name-list {
  font-weight: 600;
}

.country-name {
  font-weight: 700;
  font-size: 32px;
}

.info {
  font-weight: 500;
  font-size: 26px;
  padding-bottom: 0px;
}

.info-span {
  font-weight: 400;
  font-size: 26px;
}

li {
  list-style: none;
  padding: 0;
  margin: 0;
}
